import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import VueAxios from "vue-axios";

import i18n from "../plugins/i18n";

Vue.use(Vuex);
Vue.use(VueAxios, axios);

export default new Vuex.Store({
    state: {
        // Ганнта
        visibleCard: false,
        locale: "",
        tasks: [],
        allServices: [],
        allCoordinators: [],
        allGroups: [],
        filterGannt: {
            shortDesc: {
                findShortDesc: "",
                shortDescAccurate: false,
            },
            service: {
                checkedServices: [],
                serviceValue: ""
            },
            coordinator: {
                checkedCoordinators: [],
                coordinatorValue: ""
            },
            group: {
                checkedGroups: [],
                groupValue: ""
            }
        },
    },
    mutations: {
        // Ганнта
        UPDATE_VISIBLE_MODAL(state) {
            state.visibleCard = !state.visibleCard;
        },
        SET_LOCALE(state, locale) {
            state.locale = locale;
            i18n.locale = locale.slice(0, 2);
        },
        SET_ALL_TASKS(state, payload) {
            state.tasks = payload;
        },
        SET_ALL_SERVICES(state, payload) {
            state.allServices = payload;
        },
        SET_ALL_COORDINATORS(state, payload) {
            state.allCoordinators = payload;
        },
        SET_ALL_GROUP(state, payload) {
          state.allGroups = payload
        },
        SET_FILTERS_GANNT_BY_DESCRIPTION(state, payload) {
            state.filterGannt.shortDesc = payload;
        },
        SET_FILTERS_GANNT_BY_SERVICE(state, payload) {
            state.filterGannt.service = payload;
        },
        SET_FILTERS_GANNT_BY_COORDINATOR(state, payload) {
            state.filterGannt.coordinator = payload;
        },
        SET_FILTERS_GANNT_BY_GROUP(state, payload) {
            state.filterGannt.group = payload;
        },
    },
    actions: {
        authInfraManager() {
            axios
                .post(
                    "accountApi/SignIn",
                    JSON.stringify({
                        LoginName: "DESKTOP-DBKTI5A\\User",
                        PasswordEncrypted: "",
                    })
                )
        },
        getCurrentLocaleUser({ commit }) {
            return new Promise((resolve, reject) => {
                axios
                    .get("accountApi/GetAuthenticationInfo")
                    .then((response) => {
                        const locale = response.data.CultureName;
                        commit("SET_LOCALE", locale);
                        resolve(locale);
                    })
                    .catch((e) => {
                        console.log(e);
                        reject(e);
                    });
            });
        },
    },
    getters: {
        getVisibleModal: (state) => state.visibleCard,
        getLocale: (state) => state.locale,
        getTasks: (state) => state.tasks,
        getAllServices: (state) => state.allServices,
        getAllCoordinators: (state) => state.allCoordinators,
        getAllGroups: (state) => state.allGroups,
        getCheckedCoordinators: state => state.filterGannt.coordinator.checkedCoordinators,
        getCheckedServices: state => state.filterGannt.service.checkedServices,
        getCheckedGroups: state => state.filterGannt.group.checkedGroups,
        getFiltersGannt: (state) => state.filterGannt,
    },
    modules: {},
});
