import Vue from "vue";
import VueRouter from "vue-router";
import Gantt from "../components/Gannt/Gannt";
Vue.use(VueRouter);
const routes = [
        {
            path: '/SD/TableRFCGanntDiagramm',
            name: 'Gantt',
            component: Gantt
        }
    ]
export default new VueRouter({
    mode: "history",
    routes,
});

